import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import * as config from '../../../assets/appsettings.json';

@Injectable()
export class FaceService {
    constructor(private http: HttpClient) {
    }

    detect(blob: any) {
        const httpOptions = {
            headers: new HttpHeaders({
                'Content-Type': 'application/octet-stream',
                'Ocp-Apim-Subscription-Key': config.default.api.face.key,
            }),
        };
        const endpoint = `${config.default.api.face.endpoint}/detect`;
        return this.http.post<IDetect[]>(endpoint, blob, httpOptions);
    }

    identify(personGroupId: string, faceIds: string[]) {
        const httpOptions = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
                'Ocp-Apim-Subscription-Key': config.default.api.face.key,
            }),
        };
        const body = {
            personGroupId: personGroupId,
            faceIds: faceIds,
            maxNumOfCandidatesReturned: config.default.api.face.maxNumOfCandidatesReturned,
            confidenceThreshold: config.default.api.face.confidenceThreshold,
        }
        const endpoint = `${config.default.api.face.endpoint}/identify`;
        return this.http.post<[IIdentify]>(endpoint, body, httpOptions);
    }

    verify(personGroupId: string, personId: string, faceIds: string) {
        const httpOptions = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
                'Ocp-Apim-Subscription-Key': config.default.api.face.key,
            }),
        };
        const body = {
            personGroupId: personGroupId,
            faceId: faceIds,
            personId: personId,
        }
        const endpoint = `${config.default.api.face.endpoint}/verify`;
        return this.http.post<IVerify>(endpoint, body, httpOptions);
    }
}

export interface IDetect {
    faceId: string;
    faceRectangle: any;
    faceLandmarks: any;
    faceAttributes?: any;
}

export interface IIdentify {
    faceId: string,
    candidates: ICandidates[]
}

interface ICandidates {
    personId: string;
    confidence: number;
}

export interface IVerify {
    isIdentical: boolean;
    confidence: number
}



