import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import * as config from '../../../assets/appsettings.json';

@Injectable()
export class PersonService {
    constructor(private http: HttpClient) {
    }

    getPersonList(groupName: string): Observable<IPerson[]> {
        const httpOptions = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
                'Ocp-Apim-Subscription-Key': config.default.api.face.key,
            }),
        };
        const endpoint = `${config.default.api.face.endpoint}/persongroups/${groupName}/persons`;
        return this.http.get<IPerson[]>(endpoint, httpOptions);
    }

    createPersonFace(bodyParam: IPerson, blob: any) {
        const httpOptions = {
            headers: new HttpHeaders({
                'Content-Type': 'application/octet-stream',
                'Ocp-Apim-Subscription-Key': config.default.api.face.key,
            }),
        };

        const endpoint = `${config.default.api.face.endpoint}/persongroups/${bodyParam.personGroupId}/persons/${bodyParam.personId}/persistedFaces`;
        return this.http.post<IPerson[]>(endpoint, blob, httpOptions);
    }

    createPerson(bodyParam: IPerson) {
        const httpOptions = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
                'Ocp-Apim-Subscription-Key': config.default.api.face.key,
            }),
        };
        const body = {
            name: bodyParam.name,
            userData: bodyParam.userData,
        };
        const endpoint = `${config.default.api.face.endpoint}/persongroups/${bodyParam.personGroupId}/persons`;
        return this.http.post<IPerson[]>(endpoint, body, httpOptions);
    }

    updatePerson(bodyParam: IPerson) {
        const httpOptions = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
                'Ocp-Apim-Subscription-Key': config.default.api.face.key,
            }),
        };
        const body = {
            name: bodyParam.name,
            userData: bodyParam.userData,
        };
        const endpoint =
            `${config.default.api.face.endpoint}/persongroups/${bodyParam.personGroupId}/persons/${bodyParam.personId}`;
        return this.http.patch<IPerson[]>(endpoint, body, httpOptions);
    }

    deletePerson(bodyParam: IPerson) {
        const httpOptions = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
                'Ocp-Apim-Subscription-Key': config.default.api.face.key,
            }),
        };
        const endpoint =
            `${config.default.api.face.endpoint}/persongroups/${bodyParam.personGroupId}/persons/${bodyParam.personId}`;
        return this.http.delete<IPerson[]>(endpoint, httpOptions);
    }
}

export interface IPerson {
    personGroupId: string;
    name: string;
    userData: string;
    personId: string;
    persistedFaceIds?: string[]
}



