import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';

import * as config from '../../../assets/appsettings.json';

@Injectable()
export class GroupService {
    constructor(private http: HttpClient) {
    }

    getGroup(): Observable<IGroup[]> {
        const httpOptions = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
                'Ocp-Apim-Subscription-Key': config.default.api.face.key,
            }),
        };
        return this.http.get<IGroup[]>(`${config.default.api.face.endpoint}/persongroups`, httpOptions);
    }

    saveGroup(bodyParam: IGroup) {
        const httpOptions = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
                'Ocp-Apim-Subscription-Key': config.default.api.face.key,
            }),
        };
        const body = {
            name: bodyParam.name,
            userData: bodyParam.userData,
        };
        const endpoint = `${config.default.api.face.endpoint}/persongroups/${bodyParam.personGroupId}`;
        return this.http.put<IGroup[]>(endpoint, body, httpOptions);
    }

    trainGroup(personGroupId: string) {
        const httpOptions = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
                'Ocp-Apim-Subscription-Key': config.default.api.face.key,
            }),
        };
        const endpoint = `${config.default.api.face.endpoint}/persongroups/${personGroupId}/train`;
        return this.http.post<IGroup[]>(endpoint, {}, httpOptions);
    }

    updateData(bodyParam: IGroup) {
        const httpOptions = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
                'Ocp-Apim-Subscription-Key': config.default.api.face.key,
            }),
        };
        const body = {
            name: bodyParam.name,
            userData: bodyParam.userData,
        };
        const endpoint = `${config.default.api.face.endpoint}/persongroups/${bodyParam.personGroupId}`;
        return this.http.patch<IGroup[]>(endpoint, body, httpOptions);
    }

    deleteGroup(bodyParam: IGroup) {
        const httpOptions = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
                'Ocp-Apim-Subscription-Key': config.default.api.face.key,
            }),
        };
        const endpoint = `${config.default.api.face.endpoint}/persongroups/${bodyParam.personGroupId}`;
        return this.http.delete<IGroup[]>(endpoint, httpOptions);
    }
}

export interface IGroup {
    personGroupId: string;
    name: string;
    userData: string;
    training?: boolean;
}



